import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { environment } from 'src/environments/environment';
import { catchError } from 'rxjs/operators';

import {News} from '../models/newsModal';

const enum endpoints {
  news = '/top-headlines',
  technology = '/top-headlines?country=us&topic=technology',
  sports = '/top-headlines?country=us&topic=sports',
  science = '/top-headlines?country=us&topic=science',
  health = '/top-headlines?country=us&topic=health',
  general = '/top-headlines?country=us&topic=general',
  entertainment = '/top-headlines?country=us&topic=entertainment',
  business = '/top-headlines?country=us&topic=business',
}


@Injectable({
  providedIn: 'root'
})
export class NewsService {
  country!: string;

  private BaseUrl = 'https://gnews.io/api/v4';

  private API_KEY = environment.api;

  constructor(private _http: HttpClient) {
    this.country = 'us';
  }

  topHeading(): Observable<News> {
    return this._http
      .get<News>(
        `${this.BaseUrl}${endpoints.news}?country=${this.country}&token=${this.API_KEY}`
      )
      .pipe(catchError(this.errorHandler));
  }

  toptech(): Observable<News> {
    return this._http
    .get<News>(`${this.BaseUrl}${endpoints.technology}&token=${this.API_KEY}`)
      .pipe(catchError(this.errorHandler));
  }

  topSports(): Observable<News> {
    return this._http
    .get<News>(`${this.BaseUrl}${endpoints.sports}&token=${this.API_KEY}`)
      .pipe(catchError(this.errorHandler));
  }
  topScience(): Observable<News>{
    return this._http
    .get<News>(`${this.BaseUrl}${endpoints.science}&token=${this.API_KEY}`)
      .pipe(catchError(this.errorHandler));
  }
  topHealth(): Observable<News> {
    return this._http
    .get<News>(`${this.BaseUrl}${endpoints.health}&token=${this.API_KEY}`)
      .pipe(catchError(this.errorHandler));
  }

  topGeneral(): Observable<News> {
    return this._http
    .get<News>(`${this.BaseUrl}${endpoints.general}&token=${this.API_KEY}`)
      .pipe(catchError(this.errorHandler));
  }
  topEntertainment(): Observable<News>{
    return this._http
    .get<News>(`${this.BaseUrl}${endpoints.entertainment}&token=${this.API_KEY}`)
      .pipe(catchError(this.errorHandler));
  }

  topBusiness(): Observable<News> {
    return this._http
    .get<News>(`${this.BaseUrl}${endpoints.business}&token=${this.API_KEY}`)
      .pipe(catchError(this.errorHandler));
  }

  errorHandler(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      console.error('An error occurred:', error.error.message);
    } else {
      console.error(
        `Backend returned code ${error.status}, ` + `body was: ${error.error}`
      );
    }
    return throwError('Something bad happened; please try again later.');
  }
}

