import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { News } from 'src/app/models/newsModal';
import { NewsService } from 'src/app/service/news.service';

@Component({
  selector: 'app-health-news',
  templateUrl: './health-news.component.html',
  styleUrls: ['./health-news.component.scss']
})
export class HealthNewsComponent implements OnInit , OnDestroy{
  setNews: Subscription[] = [];

  displayHealthNews!: News;

  loading!: boolean;
  errorMessage = '';

  constructor(private newsService: NewsService) {}

  ngOnInit(): void {
    this.getScienceNews();
  }

  getScienceNews() {
    this.setNews.push(
      this.newsService.topHealth().subscribe(
        (res) => {
          this.displayHealthNews = res;
        },
        (err) => {
          console.log('Sorry ...');
          this.errorMessage = err;
          this.loading = false;
        }
      )
    );
  }

  ngOnDestroy(): void {
    this.setNews.map((s) => s.unsubscribe());
  }

}
