import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { News } from 'src/app/models/newsModal';
import { NewsService } from 'src/app/service/news.service';

@Component({
  selector: 'app-general-news',
  templateUrl: './general-news.component.html',
  styleUrls: ['./general-news.component.scss']
})
export class GeneralNewsComponent implements OnInit, OnDestroy  {
  setNews: Subscription[] = [];

  displayGeneralNews!: News;

  loading!: boolean;
  errorMessage = '';

  constructor(private newsService: NewsService) {}

  ngOnInit(): void {
    this.getScienceNews();
  }

  getScienceNews() {
    this.setNews.push(
      this.newsService.topGeneral().subscribe(
        (res) => {
          this.displayGeneralNews = res;
        },
        (err) => {
          console.log('Sorry ...');
          this.errorMessage = err;
          this.loading = false;
        }
      )
    );
  }

  ngOnDestroy(): void {
    this.setNews.map((s) => s.unsubscribe());
  }
}
