import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';

import { News } from 'src/app/models/newsModal';
import { NewsService } from 'src/app/service/news.service';

@Component({
  selector: 'app-top-news',
  templateUrl: './top-news.component.html',
  styleUrls: ['./top-news.component.scss'],
})
export class TopNewsComponent implements OnInit, OnDestroy {
  setNews: Subscription[] = [];

  displayTopNews!: News;

  loading!: boolean;
  errorMessage = '';

  constructor(private newsService: NewsService) {}

  ngOnInit(): void {
    this.getTopHeadingNews();
  }

  getTopHeadingNews() {
    this.setNews.push(
      this.newsService.topHeading().subscribe(
        (res) => {
          this.displayTopNews = res;
        },
        (err) => {
          console.log('Sorry ...');
          this.errorMessage = err;
          this.loading = false;
        }
      )
    );
  }
  ngOnDestroy(): void {
    this.setNews.map((s) => s.unsubscribe());
  }
}
