import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { News } from 'src/app/models/newsModal';
import { NewsService } from 'src/app/service/news.service';

@Component({
  selector: 'app-business-news',
  templateUrl: './business-news.component.html',
  styleUrls: ['./business-news.component.scss']
})
export class BusinessNewsComponent implements OnInit , OnDestroy {
  setNews: Subscription[] = [];

  displayBusinessNews!: News;

  loading!: boolean;
  errorMessage = '';

  constructor(private newsService: NewsService) {}

  ngOnInit(): void {
    this.getScienceNews();
  }

  getScienceNews() {
    this.setNews.push(
      this.newsService.topBusiness().subscribe(
        (res) => {
          this.displayBusinessNews = res;
        },
        (err) => {
          console.log('Sorry ...');
          this.errorMessage = err;
          this.loading = false;
        }
      )
    );
  }

  ngOnDestroy(): void {
    this.setNews.map((s) => s.unsubscribe());
  }





}
