import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { News } from 'src/app/models/newsModal';
import { NewsService } from 'src/app/service/news.service';

@Component({
  selector: 'app-science-news',
  templateUrl: './science-news.component.html',
  styleUrls: ['./science-news.component.scss'],
})
export class ScienceNewsComponent implements OnInit, OnDestroy {
  setNews: Subscription[] = [];

  displayScienceNews!: News;

  loading!: boolean;
  errorMessage = '';

  constructor(private newsService: NewsService) {}

  ngOnInit(): void {
    this.getScienceNews();
  }

  getScienceNews() {
    this.setNews.push(
      this.newsService.topScience().subscribe(
        (res) => {
          this.displayScienceNews = res;
        },
        (err) => {
          console.log('Sorry ...');
          this.errorMessage = err;
          this.loading = false;
        }
      )
    );
  }


  ngOnDestroy(): void {
    this.setNews.map((s) => s.unsubscribe());
  }

}
