import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { BusinessNewsComponent } from './components/business-news/business-news.component';
import { EntertainmentNewsComponent } from './components/entertainment-news/entertainment-news.component';
import { GeneralNewsComponent } from './components/general-news/general-news.component';
import { HealthNewsComponent } from './components/health-news/health-news.component';
import { ScienceNewsComponent } from './components/science-news/science-news.component';
import { SportsNewsComponent } from './components/sports-news/sports-news.component';
import { TechnologyNewsComponent } from './components/technology-news/technology-news.component';
import { TopNewsComponent } from './components/top-news/top-news.component';

const routes: Routes = [
  {path:'', component:TopNewsComponent},
  {path:'tech', component:TechnologyNewsComponent},
  {path:'sports',component:SportsNewsComponent},
  {path:'science',component:ScienceNewsComponent},
  {path:'health',component:HealthNewsComponent},
  {path:'general',component:GeneralNewsComponent},
  {path:'entertainment',component:EntertainmentNewsComponent},
  {path:'business', component:BusinessNewsComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
