import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { News } from 'src/app/models/newsModal';
import { NewsService } from 'src/app/service/news.service';

@Component({
  selector: 'app-sports-news',
  templateUrl: './sports-news.component.html',
  styleUrls: ['./sports-news.component.scss'],
})
export class SportsNewsComponent implements OnInit , OnDestroy{
  setNews: Subscription[] = [];

  displaySportsNews!: News;

  loading!: boolean;
  errorMessage = '';

  constructor(private newsService: NewsService) {}

  ngOnInit(): void {
    this.getSportsNews();
  }

  getSportsNews() {
    this.setNews.push(
      this.newsService.topSports().subscribe(
        (res) => {
          this.displaySportsNews = res;
        },
        (err) => {
          console.log('Sorry ...');
          this.errorMessage = err;
          this.loading = false;
        }
      )
    );
  }

  ngOnDestroy(): void {
    this.setNews.map((s) => s.unsubscribe());
  }
}
