import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { News } from 'src/app/models/newsModal';
import { NewsService } from 'src/app/service/news.service';

@Component({
  selector: 'app-technology-news',
  templateUrl: './technology-news.component.html',
  styleUrls: ['./technology-news.component.scss']
})
export class TechnologyNewsComponent implements OnInit, OnDestroy {
  setNews: Subscription[] = [];

  displayTechNews!: News;

  loading!: boolean;
  errorMessage = '';


  constructor(private newsService: NewsService) {}

  ngOnInit(): void {
    this.getTechnologyNews();
  }

  getTechnologyNews() {
    this.setNews.push(
      this.newsService.toptech().subscribe(
        (res) => {
          this.displayTechNews = res;
        },
        (err) => {
          console.log('Sorry ...');
          this.errorMessage = err;
          this.loading = false;
        }
      )
    );
  }

  ngOnDestroy(): void {
    this.setNews.map((s) => s.unsubscribe());
  }

}
